.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-radius-left {
  position: absolute;
  width: 367px;
  height: 225px;
  left: 0;
  top: 1536px;
  /* background: url(../../Assets/icons/footer-radius-left.svg) no-repeat; */
  z-index: -1;
}

.footer-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-image: url(/public/images/footer-bgr.png);
  mix-blend-mode: difference;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  mix-blend-mode: difference;
  border-radius: 0px 7px 7px 0px;
  gap: 16px;
}

.footer-headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 67px;
}

.footer-headline h3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 16px;
  width: 100%;
  max-width: 371px;
  font-weight: 700;
  font-size: 26px;
  line-height: 39px;
  color: #ffffff;
}

.footer-headline p {
  width: 100%;
  max-width: 588px;
  margin-top: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: rgba(255, 255, 255, 0.85);
}

.footer-search {
  display: flex;
  align-items: center;
  margin-bottom: 68px;
  width: 100%;
  max-width: 252px;
  gap: 12px;
}

.footer-search__enter-box {
  max-width: 175px;
  height: 36px;
  padding-left: 8px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.56);
  background: rgba(224, 163, 201, 0.56);
  border-radius: 8px 0px 0px 8px;
  border: none;
}

.footer-search__enter-box::placeholder {
  color: rgba(255, 255, 255, 0.56);
}

.footer-search__btn {
  padding: 6px 12px;
  width: 100%;
  max-width: 65px;
  max-height: 36px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #f4f9fe;
  text-align: center;
  background: linear-gradient(
    99.99deg,
    #b936f5 -21.45%,
    #c64c85 100%,
    #f1005b 100%
  );
  border-radius: 0px 8px 8px 0px;
  border: 0px solid transparent;
  cursor: pointer;
  color: var(--perfect-white);
}

.footer-search__btn:hover {
  background: linear-gradient(rgb(192, 51, 223), rgb(185, 53, 123));
}

.alert-message {
  position: fixed;
  bottom: 0;
  background-color: green;
  color: white;
  padding: 10px;
}

