@media (max-width: 921px) {
  /* footer */
  .footer-headline h3 {
    font-size: 20px;
  }

  .footer-headline p {
    font-weight: 400;
    font-size: 13px;
    line-height: 11px;
    max-width: 336px;
  }
}

@media (max-width: 766px) {
  /* footer */

  .footer-headline p {
    font-weight: 400;
    font-size: 13px;
    line-height: 11px;
    max-width: 336px;
  }
}

@media (max-width: 576px) {
  /* footer */

  .footer-radius-left {
    display: none;
  }

  .footer-wrap {
    padding-top: 10px;
    padding-bottom: 10px;
    background-image: url(/public/icons/footer-mobile.svg);
    mix-blend-mode: difference;
    border-radius: 8px;
    gap: 10px;
  }

  .footer-headline {
    width: 100%;
    margin-top: 10px;
  }

  .footer-headline h3 {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
  }

  .footer-headline p {
    font-weight: 400;
    font-size: 10px;
    line-height: 11px;
    color: rgba(255, 255, 255, 0.85);
    max-width: 253px;
  }

  .footer-search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    gap: 12px;
  }

  .footer-search__enter-box {
    display: flex;
    padding: 16.5px 8px;
    width: 100%;
    max-width: 175px;
    max-height: 33px;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: rgba(255, 255, 255, 0.56);
    background: rgba(224, 163, 201, 0.56);
    box-shadow: inset 0px -9.72759px 16.2127px rgba(255, 255, 255, 0.03);
    backdrop-filter: blur(6.07974px);
    border-radius: 8px 0px 0px 8px;
  }

  .footer-search__btn {
    display: flex;
    width: 100%;
    height: 100%;
    max-width: 60px;
    max-height: 33px;
    padding: 6px 12px;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: #f4f9fe;
    background: linear-gradient(
      99.99deg,
      #b936f5 -21.45%,
      #c64c85 100%,
      #f1005b 100%
    );
    border-radius: 0px 8px 8px 0px;
  }

  .footer-headline p {
    width: 100%;
    max-width: 253px;
  }
}

